import { AppreciationCardData, APPRECIATION_CARD_STATE, AppreciationTopic, AppreciationSubTopic, MinimizedAppreciationCard, ConfirmationMessage, TOKEN } from '../interfaces/iac';
import Person from './person';

export interface MockAppreciationCardData {
  id? : number;
  sender?: Person;
  receiver?: Person;
  message?: string;
  dateCreated?: number;
  dateSent?: number;
  state?: number;
  topic?: AppreciationTopic;
  subtopics?: Array < AppreciationSubTopic > ;
  token?: number;
  bonus?: number;
  streak?: number;
}

class MockAppreciationCard {
  private _id: number;
  private _sender: Person;
  private _receiver: Person;
  private _message: string;
  private _dateCreated: number;
  private _dateSent: number;
  private _state: APPRECIATION_CARD_STATE;
  private _topic: AppreciationTopic;
  private _subtopics: Array<AppreciationSubTopic>;
  private _token: TOKEN;
  private _bonus: number;
  private _streak: number;

  constructor(macd: MockAppreciationCardData) {
    this._id = macd.id ? macd.id : -1;
    this._sender = macd.sender ? macd.sender : new Person({
      name: "Alice",
      email: "alice@alice.alice",
      deleted: false
    });
    this._receiver = macd.receiver ? macd.receiver : new Person({
      name: "Bob",
      email: "bob@bob.bob",
      deleted: false
    });
    this._message = macd.message ? macd.message : "Reasoning goes here.";
    this._dateCreated = macd.dateCreated ? macd.dateCreated : Date.now();
    this._dateSent = macd.dateSent ? macd.dateSent : Date.now();
    this._state = macd.state ? macd.state : APPRECIATION_CARD_STATE.RECEIVED;

    this._topic = macd.topic ? macd.topic : {
      id: -1,
      text: 'Appreciation topic goes here',
      deleted: false,
      color: 'primary',
      icon: 'mdi-party-popper',
      subtopics: [
        {
          id: -1,
          text: 'Appreciation subtopic goes here',
          deleted: false
        }
      ] 
    }
    this._subtopics = macd.subtopics ? macd.subtopics : this._topic.subtopics;

    this._token = macd.token ? macd.token : TOKEN.NONE;
    this._bonus = macd.bonus ? macd.bonus : 0;
    this._streak = macd.streak ? macd.streak : -1;
  }

  public minimize(includePerson: boolean): MinimizedAppreciationCard {
    const msg: MinimizedAppreciationCard = {
      e: this._sender.email,
      m: this._message,
      d: this._dateCreated,
      t: this._topic.id,
      s: this._subtopics.map((subtopic: AppreciationSubTopic) => subtopic.id)
    };
    if (includePerson) msg.p = this._sender.minimize(false);
    return msg;
  }

  public toACData(): AppreciationCardData {
    const msg: AppreciationCardData = {
      id: this._id,
      sender: this._sender.email,
      receiver: this._receiver.email,
      message: this._message,
      dateCreated: this._dateCreated,
      dateSent: this._dateSent,
      state: this._state,
      topic: this._topic.id,
      subtopics: this._subtopics.map((subtopic: AppreciationSubTopic) => subtopic.id)
    };
    return msg;
  }

  public confirm(includePerson: boolean): ConfirmationMessage {
    const msg: ConfirmationMessage = {
      e: this._receiver.email,
      d: this._dateCreated,
      n: this.dateSent
    }
    if (includePerson) msg.p = this._receiver.minimize(false);
    return msg;
  }

  // Assume that the sender can't make two different cards in the same millisecond
  public equals(mac: MinimizedAppreciationCard) {
    return (
      this._sender.email === mac.e
      && this._dateCreated === mac.d
    )
  }

  get id(): number {
    return this._id
  }
  get sender(): Person {
    return this._sender
  }
  get receiver(): Person {
    return this._receiver
  }
  get message(): string {
    return this._message
  }
  get dateCreated(): number {
    return this._dateCreated
  }
  get dateSent(): number {
    return this._dateSent
  }
  set dateSent(date: number) {
    this._dateSent = date;
  }
  get state(): APPRECIATION_CARD_STATE {
    return this._state
  }
  set state(s: APPRECIATION_CARD_STATE) {
    this._state = s;
  }
  get topic(): AppreciationTopic {
    return this._topic
  }
  get subtopics(): Array<AppreciationSubTopic> {
    return this._subtopics
  }
  get token(): TOKEN {
    return this._token;
  }
  get bonus(): number {
    return this._bonus;
  }
  get streak(): number {
    return this._streak;
  }

}

export default MockAppreciationCard;