



















































































import Vue from 'vue';
import AppreciationCard from '@/data/ac';
import Person from '@/data/person';
import { APPRECIATION_CARD_STATE } from '@/interfaces/iac';
import iconConfig from '@/store/misc/icons';
import { acModule } from '../store/modules/acs';

export default Vue.extend({
  name: 'Card',
  props: {
    ac: Object as () => AppreciationCard
  },
  data() { 
    return {
      swap: true,
      iconConfig
    }
  },
  computed: {
    person(): Person | undefined {
      return this.ac 
        ? this.ac.state === APPRECIATION_CARD_STATE.RECEIVED 
          ? this.ac.sender 
          : this.ac.receiver 
        : undefined;
    },
    senderReceiverTokenIcon(): string {
      return this.ac.state === APPRECIATION_CARD_STATE.SENT 
        ? iconConfig.rewardToken
        : iconConfig.awesomeToken;
    },
    tokensEarned(): number {
      return this.ac.state === APPRECIATION_CARD_STATE.RECEIVED 
        ? acModule.tokenConfig.AWESOME_TOKENS_PER_CARD + this.ac.bonus
        : acModule.tokenConfig.REWARD_TOKENS_PER_CARD + this.ac.bonus;
    },
    dateString(): string {
      return this.ac 
        ? this.ac.state === APPRECIATION_CARD_STATE.RECEIVED 
          ? 'Received on ' + new Date(this.ac.dateSent).toDateString()
          : 'Sent on ' + new Date(this.ac.dateSent).toDateString()
        : '';
    },
  },
  methods: {
    imageSource(person: Person): string {
      return person.image ? require(`@/assets/${person.image.path}`) : '';
    }
  },
});
